<template>
    <div class="todo-item" :class="{'is-complete':todo.completed}">
        <p>
            <input type="checkbox" @change="markComplete">
            {{todo.title}}
            <!-- emit sends props to top -->
            <button @click="$emit('del-todo', todo.id)" class="del">X</button>    
        </p>
    </div>
</template>

<script>
export default {
    name: 'TodoItem',
    props: ['todo'],
    methods: {
        markComplete() {
            this.todo.completed = !this.todo.completed
        }
    }
}
</script>

<style scoped>
    .todo-item {
        background: #f4f4f4;
        padding: 1rem;
        border-bottom: 1px dotted #ccc;
    }
    .is-complete {
        text-decoration: line-through;
    }
    .del{
        background: #ff0000;
        color: #fff;
        border: none;
        padding: .5rem 1rem;
        border-radius: 50%;
        cursor: pointer;
        float: right;

    }
</style>