<template>
    <div>
        <form @submit="addTodo">

            <input v-model="title" type="text" name="title" placeholder="Add Todo">
            <input type="submit" value="Submit" class="btn">
        </form>

    </div>
</template>

<script>
export default {
    name: 'AddTodo',
    data() {
        return {
            title: '',
        }
    },
    methods: {
        addTodo(e) {
            e.preventDefault()
            const newTodo = {
                //JsonPlaceHolder givs an id so its not needed to generate one
                // id: Date.now(),
                title: this.title,
                completed: false
            }

            // send up to parent
            this.$emit('add-todo', newTodo)

            this.title = ''
        }
        
    }
}

</script>

<style scoped>
    form{
        display: flex;
    }
    input[type="text"] {
        flex: 10;
        padding: .5rem;
    }
    input[type="submit"]{
        flex: 2;
    }
</style>